@charset "UTF-8";
label {
  //font-size: 12px;
  font-weight: normal;
}

.tips {
  position: relative;
  top: 2px;
  margin-right: 2px;
  font-style: normal;
  color: $brand-danger;
}

// 日历
.Wdate {
  width: 220px !important;
  height: 30px !important;
}

// 按钮
.btn {
  min-width: 100px;
  border-radius: 2px;
  &:active {
    outline: none;
  }
}

// 表单
.form-control {
  height: 30px;
  padding: 3px 6px;
  //font-size: 12px;
  border-radius: 2px;
}

.form-control-static {
  font-size: 12px;
}

// 弹出层
.modal-header {
  padding: 10px 20px;
}

.modal-title {
  font-size: 14px;
}

// tab
.nav-tabs > li > a {
  font-size: 12px;
}

.tab-content > .tab-pane {
  padding-top: 10px;
}

// 分页
.pagination li > a, .pagination li > span {
  color: $brand-danger;
}

.pagination li.active > a, .pagination li.active > span, .pagination li.active > a:hover, .pagination li.active > span:hover, .pagination li.active > a:focus, .pagination li.active > span:focus {
  background-color: $brand-danger;
  cursor: pointer;
}

.pagination li > a:hover, .pagination li > span:hover, .pagination li > a:focus, .pagination li > span:focus {
  background-color: $brand-danger;
  cursor: pointer;
}