
// 公共框架样式
.page-iframe {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: auto;
  background: #e5e6e6;
}

.content {
  position: fixed;
  left: 250px;
  top: 60px;
  right: 0;
  bottom: 0;
  overflow: hidden;
}

.card-body {
  position: relative;
  padding: 20px;
}

.page-title {
  height: 50px;
  line-height: 50px;
  border-bottom: 2px solid $gray-lighter;
  margin-bottom: 30px;
  h3 {
    display: inline-block;
    padding-bottom: 14px;
    margin-top: 0;
    font-size: 18px;
    font-weight: normal;
    border-bottom: 2px solid $brand-danger;
  }
}

.form-submit {
  position: absolute;
  top: 25px;
  right: 15px;
  .btn {
    margin: 0 5px;
  }
}

.section-body {
  margin-top: 10px;
  overflow-x: auto;
}



// 筛选条件
.sl-wrap {
  background-color: $gray-lighter;
}

.sl-inner {
  width: 1020px;
  padding: 10px;
  border-bottom: 1px dotted #ccc;
  font-size: 12px;
  select,
  input,
  .btn {
    font-size: 12px;
  }
  select option {
    padding: 3px 0;
  }
}

.form-wrap{
  width: 1000px;
}

.form-group{
  @include clearfix();
}
.max-box,
.divide2-box,
.divide3-box{
  @include clearfix();
  .control-label{
    float: left;
    width: 120px;
    padding-top: 6px;
    margin-right: 6px;
    margin-bottom: 0;
    line-height: 20px;
    text-align: right;
  }
  .btn{
    padding:6px 12px;
  }
}
.max-box{
  .form-control{
    width: 867px;
    height: 34px;
    color: $gray-dark;
  }
  .select-form{
    width: 600px;
    float: left;
    margin-right:20px;
  }
}

.divide2-box{
  width: 50%;
  float: left;
  .form-control {
    min-width: 120px;
    max-width: 200px;
    height: 34px;
    color: $gray-dark;
  }
  .select-form{
    width: 200px;
    float: left;
    margin-right:20px;
  }
}
.divide3-box{
  width: 33.3333%;
  float: left;
  .form-control {
    min-width: 120px;
    max-width: 200px;
    height: 34px;
    color: $gray-dark;
  }
}

.file{
  margin-left: 130px;
  margin-top: 10px;
}
.icon-file{
  position: relative;
  top: 4px;
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 5px;
  background: url("../img/icon_file.png") no-repeat 0 0;
}
.radio{
  margin-top: 2px;
  label{
    margin-right: 30px;
  }
  input[type="radio"]{
    position: relative;
    top: 2px;
    margin-right: 5px;
  }
}
.submit-wrap{
  margin-top: 50px;
  text-align: center;
  .btn{
    margin-left: 20px;
    margin-right: 20px;
  }
}


.radio-box{
  @include clearfix();
  padding: 20px 20px 10px;
  background-color: $gray-lighter;
}

.mod-title{
  font-size: 16px;
  line-height: 50px;
  color: $brand-info;
}

.btn-operate{
  padding-left: 10px;
  padding-right: 10px;
}



.sl-key {
  float: left;
  display: inline-block;
  padding: 0 20px;
  height: 30px;
  color: $brand-danger;
  line-height: 30px;
}

.sl-value {
  float: left;
  line-height: 30px;
  .form-group {
    margin-right: 20px;
  }
  label {
    float: left;
    width: 80px;
    padding-top: 6px;
    margin-right: 6px;
    margin-bottom: 0;
    line-height: 20px;
    text-align: right;
  }
  .form-control {
    min-width: 140px;
    max-width: 220px;
  }
}


// 表单查询数据页面
.section-body {
  table {
    word-break: break-all;
    //font-size: 12px;
    th,
    td {
      position: relative;
      min-width: 100px;
      max-width: 220px;
      text-align: center;
      white-space: nowrap;
      .cell {
        display: inline-block;
        min-width: 100px;
        max-width: 210px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        cursor: pointer;
      }
    }
  }
}

// 弹出层
.modal-dialog {
  width: 600px;
}

.modal-body {
  padding: 20px;
}

.md-key {
  float: left;
  width: 80px;
  text-align: right;
}

.md-value {
  float: left;
  width: 472px;
  margin-left: 20px;
}

// 表单添加
.form-add {

}

.form-content {
  .form-group {
    float: left;
    display: inline-block;
    @include clearfix();
    margin-bottom: 20px;
  }
  label {
    width: 100px;
    margin-right: 6px;
    margin-bottom: 0;
    text-align: right;
  }
  input, select {
    &.form-control {
      width: 280px;
    }
  }
  select {
    margin-left: -4px;
  }
}

.other-info {
  padding: 20px;
  p {
    font-size: 12px;
  }
}

.img-upload {
  margin-top: 10px;
  margin-left: -5px;
  margin-right: -5px;
  li {
    position: relative;
    float: left;
    width: 200px;
    height: 140px;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 10px;
    line-height: 140px;
    background-color: $gray-lighter;
    overflow: hidden;
    img {
      position: absolute;
      top: 50%;
      display: inline-block;
      transform: translateY(-50%);
    }
    .closed {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 100;
      width: 20px;
      height: 20px;
      text-align: center;
      line-height: 20px;
      color: #fff;
      font-size: 12px;
      font-style: normal;
      background-color: $brand-danger;
      border-radius: 50%;
      cursor: pointer;
      &:hover {
        background-color: darken($brand-danger, 5%);
      }
    }
  }
}

// 内页
.form-show {
  font-size: 12px;
  label {
    color: $gray-light;
  }
  .value {
    display: inline-block;
    width: 220px;
  }
}