@charset "UTF-8";
.head-wrap {
  position: fixed;
  z-index: 9999;
  width: 100%;
  height: 60px;
  background-color: #F2F3F3;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
}

.logo-admin {
  float: left;
  width: 250px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  background-color: $brand-primary;
  a {
    color: #fff;
    font-size: 20px;
    font-weight: 500;
  }
}

.nav-admin {
  .name{
    margin-right: 10px;
    color: $brand-primary;
  }
  li{
    float: left;
    display: inline-block;
    height: 60px;
  }
  .active{
    background-color: #fcd9dd;
    a{
      //color: #fff;
    }
  }
  a {
    display: block;
    padding: 20px;
    line-height: 1.5;
    color: $gray-darker;
    &:hover{
      color: $brand-primary;
    }
  }
  .nav-inline{
    float: left;
  }
  .nav-tool{
    position: absolute;
    top: 0;
    right: 0;
  }
}


