@charset "UTF-8";
.menubar-panel {
  position: fixed;
  top: 60px;
  bottom: 0;
  z-index: 1000;
  width: 250px;
  background-color: #F2F3F3;
  overflow-y: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
}

.menu-content {
  padding-top: 20px;
  //li {
  //  border-left: 5px solid transparent;
  //}
  a {
    display: block;
    padding: 10px 20px;
    &:hover{
      color: $brand-primary;
    }
  }
  li:hover,.active{
    background-color: #fff;
    border-left: 5px solid $brand-primary;
    transition: border-left 0.1s;
  }
}

.sidebar-btn{
  position: absolute;
  top: 12px;
  right: 15px;
  z-index: 1000;
}

.tree {
  min-height:20px;
  padding:15px 0;
  margin-bottom:20px;
}
.tree li {
  position:relative;
  margin:0;
  padding:10px 5px 0 20px;
}
.tree li::before, .tree li::after {
  content:'';
  left:0;
  position:absolute;
  right:auto
}
.tree li::before {
  border-left:1px solid #999;
  bottom:50px;
  height:100%;
  top:0;
  width:1px
}
.tree li::after {
  border-top:1px solid #999;
  height:20px;
  top:25px;
  width:25px
}
.tree li span {
  display:inline-block;
  padding:3px 8px;
  text-decoration:none
}
.tree li.parent_li>span {
  cursor:pointer
}
.tree>ul>li::before, .tree>ul>li::after {
  border:0
}
.tree li:last-child::before {
  height:26px
}
.tree li.parent_li>span:hover, .tree li.parent_li>span:hover+ul li span {
  color: #ff0000;
}

.icon-plus-sign{
  display: inline-block;
  width: 18px;
  height: 18px;
  background: url(../img/icon-folder.gif) 0 0 no-repeat;
}
.icon-minus-sign{
  display: inline-block;
  width: 18px;
  height: 18px;
  background: url(../img/icon-folder-open.gif) 0 0 no-repeat;
}

.icon-folder-open{
  display: inline-block;
  width: 18px;
  height: 18px;
  background: url(../img/icon-folder.gif) 0 0 no-repeat;
  &.icon-plus-sign{
    display: inline-block;
    width: 18px;
    height: 18px;
    background: url(../img/icon-folder.gif) 0 0 no-repeat;
  }
  &.icon-minus-sign{
    display: inline-block;
    width: 18px;
    height: 18px;
    background: url(../img/icon-folder-open.gif) 0 0 no-repeat;
  }
}



